"use client";

import { useEffect, useState, useMemo } from "react";
import Sidebar from "@/components/sidebar2";
import { ChevronLeft, ChevronRight, Package } from "lucide-react";
import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";
import { prisma } from "@/lib/prisma"

// Example Products (replace with your actual data source)
const products = await prisma.productList.findMany()

interface ReportData {
    productName: string;
    locationName: string;
    openingStock: number;
    receipts: number;
    sales: number;
    transfersIn: number;
    transfersOut: number;
    returns: number;
    damaged: number;
    expired: number;
    rebagGain: number;
    rebagLoss: number;
    closingStock: number;
}

const ITEMS_PER_PAGE = 15;

export default function StockReportDashboard() {
    const [report, setReport] = useState<ReportData[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [currentPage, setCurrentPage] = useState(1);
    const [selectedLocations, setSelectedLocations] = useState<string[]>([]);
    const [selectedCategories, setSelectedCategories] = useState<string[]>([]);

    // Map productName â†’ category
    const productCategoryMap = useMemo(() => {
        const map: Record<string, string> = {};
        products.forEach((p) => (map[p.name] = p.category));
        return map;
    }, []);

    // Location & Category options
    const locationOptions = useMemo(() => {
        const locations = Array.from(new Set(report.map((r) => r.locationName)));
        return locations.map((l) => ({ value: l, label: l }));
    }, [report]);

    const categoryOptions = useMemo(() => {
        const categories = Array.from(new Set(products.map((p) => p.category)));
        return categories.map((c) => ({ value: c, label: c }));
    }, []);

    // Fetch report data
    useEffect(() => {
        const fetchReport = async () => {
            try {
                const res = await fetch("/api/stock-report");
                const data = await res.json();
                setReport(data);
            } catch (err) {
                console.error(err);
                setError("Failed to fetch stock report");
            } finally {
                setLoading(false);
            }
        };
        fetchReport();
    }, []);

    useEffect(() => {
        setCurrentPage(1); // Reset page on report update
    }, [report, selectedLocations, selectedCategories]);

    // Filtered report
    const filteredReport = useMemo(() => {
        return report.filter((r) => {
            const category = productCategoryMap[r.productName] || "Uncategorized";

            if (selectedLocations.length > 0 && !selectedLocations.includes(r.locationName)) return false;
            if (selectedCategories.length > 0 && !selectedCategories.includes(category)) return false;

            return true;
        });
    }, [report, selectedLocations, selectedCategories, productCategoryMap]);

    // Pagination
    const totalPages = Math.ceil(filteredReport.length / ITEMS_PER_PAGE);
    const paginatedReport = filteredReport.slice(
        (currentPage - 1) * ITEMS_PER_PAGE,
        currentPage * ITEMS_PER_PAGE
    );

    // Export CSV
    const exportCSV = () => {
        const headers = [
            "Product",
            "Category",
            "Location",
            "Opening Stock",
            "Receipts",
            "Sales",
            "Transfers In",
            "Transfers Out",
            "Returns",
            "Damaged",
            "Expired",
            "Rebag Gain",
            "Rebag Loss",
            "Closing Stock",
        ];

        const rows = filteredReport.map((r) => [
            r.productName,
            productCategoryMap[r.productName] || "Uncategorized",
            r.locationName,
            r.openingStock,
            r.receipts,
            r.sales,
            r.transfersIn,
            r.transfersOut,
            r.returns,
            r.damaged,
            r.expired,
            r.rebagGain,
            r.rebagLoss,
            r.closingStock,
        ]);

        const csv = [headers, ...rows].map((r) => r.join(",")).join("\n");
        const blob = new Blob([csv], { type: "text/csv" });
        const url = URL.createObjectURL(blob);
        const a = document.createElement("a");
        a.href = url;
        a.download = "stock_report.csv";
        a.click();
        URL.revokeObjectURL(url);
    };

    // Export PDF
    const exportPDF = () => {
        const doc = new jsPDF();
        doc.setFontSize(16);
        doc.text("Stock Report", doc.internal.pageSize.getWidth() / 2, 15, { align: "center" });

        autoTable(doc, {
            startY: 25,
            head: [
                [
                    "Product",
                    "Category",
                    "Location",
                    "Opening",
                    "Receipts",
                    "Sales",
                    "Transfers In",
                    "Transfers Out",
                    "Returns",
                    "Damaged",
                    "Expired",
                    "Rebag Gain",
                    "Rebag Loss",
                    "Closing",
                ],
            ],
            body: filteredReport.map((r) => [
                r.productName,
                productCategoryMap[r.productName] || "Uncategorized",
                r.locationName,
                r.openingStock,
                r.receipts,
                r.sales,
                r.transfersIn,
                r.transfersOut,
                r.returns,
                r.damaged,
                r.expired,
                r.rebagGain,
                r.rebagLoss,
                r.closingStock,
            ]),
            styles: { fontSize: 8, cellPadding: 3 },
            headStyles: { fillColor: [100, 100, 100], textColor: 255 },
            alternateRowStyles: { fillColor: [245, 245, 245] },
        });

        doc.save("stock_report.pdf");
    };

    if (loading) return <div>Loading...</div>;
    if (error) return <div>{error}</div>;

    return (
        <main>
            <Sidebar />

            <div className="ml-64 p-4">
                <h2 className="text-2xl font-bold mb-4 flex items-center gap-2">
                    <Package className="h-5 w-5" />
                    Stock Report
                </h2>

                {/* Filters & Exports */}
                <div className="flex flex-wrap gap-2 mb-4">
                    {/* Location Filter */}
                    <SelectDropdown
                        title="Select Locations"
                        options={locationOptions}
                        selected={selectedLocations}
                        setSelected={setSelectedLocations}
                    />
                    {/* Category Filter */}
                    <SelectDropdown
                        title="Select Categories"
                        options={categoryOptions}
                        selected={selectedCategories}
                        setSelected={setSelectedCategories}
                    />
                    <button
                        onClick={exportCSV}
                        className="px-2 py-1 h-8 border rounded hover:bg-gray-200"
                    >
                        Export CSV
                    </button>
                    <button
                        onClick={exportPDF}
                        className="px-2 py-1 h-8 border rounded hover:bg-gray-200"
                    >
                        Export PDF
                    </button>
                </div>

                {/* Table */}
                <div className="overflow-x-auto">
                    <table className="w-full border-collapse border">
                        <thead className="bg-gray-200">
                            <tr>
                                <th className="border p-2">Product</th>
                                <th className="border p-2">Category</th>
                                <th className="border p-2">Location</th>
                                <th className="border p-2">Opening</th>
                                <th className="border p-2">Receipts</th>
                                <th className="border p-2">Sales</th>
                                <th className="border p-2">Transfers In</th>
                                <th className="border p-2">Transfers Out</th>
                                <th className="border p-2">Returns</th>
                                <th className="border p-2">Damaged</th>
                                <th className="border p-2">Expired</th>
                                <th className="border p-2">Rebag Gain</th>
                                <th className="border p-2">Rebag Loss</th>
                                <th className="border p-2">Closing</th>
                            </tr>
                        </thead>
                        <tbody>
                            {paginatedReport.map((r, idx) => (
                                <tr key={idx} className="hover:bg-gray-100">
                                    <td className="border p-2">{r.productName}</td>
                                    <td className="border p-2">{productCategoryMap[r.productName] || "Uncategorized"}</td>
                                    <td className="border p-2">{r.locationName}</td>
                                    <td className="border p-2">{r.openingStock}</td>
                                    <td className="border p-2">{r.receipts}</td>
                                    <td className="border p-2">{r.sales}</td>
                                    <td className="border p-2">{r.transfersIn}</td>
                                    <td className="border p-2">{r.transfersOut}</td>
                                    <td className="border p-2">{r.returns}</td>
                                    <td className="border p-2">{r.damaged}</td>
                                    <td className="border p-2">{r.expired}</td>
                                    <td className="border p-2">{r.rebagGain}</td>
                                    <td className="border p-2">{r.rebagLoss}</td>
                                    <td className="border p-2">{r.closingStock}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>

                {/* Pagination */}
                <ClientPagination
                    currentPage={currentPage}
                    totalPages={totalPages}
                    onPageChange={setCurrentPage}
                />
            </div>
        </main>
    );
}

// Simple multi-select dropdown component
function SelectDropdown({
    title,
    options,
    selected,
    setSelected,
}: {
    title: string;
    options: { value: string; label: string }[];
    selected: string[];
    setSelected: (val: string[]) => void;
}) {
    const [show, setShow] = useState(false);

    const toggleOption = (value: string) => {
        if (selected.includes(value)) setSelected(selected.filter((v) => v !== value));
        else setSelected([...selected, value]);
    };

    return (
        <div className="relative">
            <button
                onClick={() => setShow(!show)}
                className="px-3 py-1 h-8 border rounded hover:bg-gray-200"
            >
                {title} ({selected.length})
            </button>
            {show && (
                <div className="absolute mt-1 bg-white border rounded shadow-lg z-20 w-48 max-h-48 overflow-y-auto p-2">
                    <button
                        onClick={() => setSelected([])}
                        className="text-sm text-blue-500 hover:underline mb-2"
                    >
                        Clear Selection
                    </button>
                    {options.map((opt) => (
                        <div key={opt.value} className="flex items-center gap-2 mb-1">
                            <input
                                type="checkbox"
                                checked={selected.includes(opt.value)}
                                onChange={() => toggleOption(opt.value)}
                                className="w-4 h-4"
                            />
                            <label>{opt.label}</label>
                        </div>
                    ))}
                </div>
            )}
        </div>
    );
}

// Pagination component
function ClientPagination({
    currentPage,
    totalPages,
    onPageChange,
}: {
    currentPage: number;
    totalPages: number;
    onPageChange: (page: number) => void;
}) {
    if (totalPages <= 1) return null;

    return (
        <div className="flex justify-center gap-2 mt-4">
            <button
                disabled={currentPage === 1}
                onClick={() => onPageChange(currentPage - 1)}
                className="px-3 py-1 border rounded bg-black text-white hover:bg-purple-600 disabled:opacity-50"
            >
                <ChevronLeft />
            </button>
            <span className="px-3 py-1">Page {currentPage} of {totalPages}</span>
            <button
                disabled={currentPage === totalPages}
                onClick={() => onPageChange(currentPage + 1)}
                className="px-3 py-1 border rounded bg-black text-white hover:bg-purple-600 disabled:opacity-50"
            >
                <ChevronRight />
            </button>
        </div>
    );
}
